:root {
    --header-h: 80px;
    /* 헤더 실제 높이 */
}

* {
    font-family: "GyeonggiMillenniumTitle", "맑은 고딕", "Segoe UI", Tahoma, Geneva, sans-serif;
}

#project {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    width: 100%;
    height: calc(100vh - var(--header-h));
    box-sizing: border-box;

}

.projectContainer {
    display: grid;
    grid-template-columns: 48px 1fr 48px;
    /* ← 왼쪽 화살표 / 콘텐츠 / 오른쪽 화살표 */
    justify-content: space-evenly;
    align-items: center;
    width: 1440px;
    height: 800px;
    box-sizing: border-box;
    border: 3px solid #f9c300;
    column-gap: 3px;
}

/* 부모 규격은 그대로 사용 (#project, .projectContainer) */

/* 화살표 */
.projArrow {
    width: 45px;
    height: 45px;
    background: transparent;
    color: #1e3a5f;
    font-size: 34px;
    cursor: pointer;
    transition: transform .15s ease, background .15s ease;
    text-align: center;
    border-radius: 50%;
    font-weight: 700;
}



.projArrow:hover {
    background-color: rgb(238, 242, 255);

}

/* 카드 컨테이너(1440×950 내부에서 패딩만 줌) */
.projCard {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    height: 100%;
    padding: 28px 28px 20px;
    box-sizing: border-box;
    overflow: hidden;
    /* 부모 높이 넘지 않도록 */


}

/* 상단 제목/스택 */
.projHead {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;
    padding-bottom: 10px;
    border-bottom: 2px solid #f9c300;
    margin-bottom: 30px;

}

.projTitle {
    margin: 0;
    font-size: 28px;
    letter-spacing: 1px;
    color: #1e3a5f;
    font-weight: 800;
}

.projTitle span {
    padding: 5px 20px;
    border-radius: 20px;
    background-color: #f9c300;
    margin-left: 20px;
    font-weight: 800;
}

.projStack {
    display: flex;
    gap: 8px;
    margin: 0;
    padding: 0;
    list-style: none;
    flex-wrap: wrap;
}

.stackItem {
    padding: 6px 10px;
    font-size: 12px;
    border-radius: 50px;
    background: #eef2ff;
    color: #364e66;
    border: 1px solid #c7d2fe;
}

/* 메인 2열 */
.projMain {
    display: grid;
    grid-template-columns: 640px 1fr;
    gap: 15px;

}

/* 모니터 블록 */
.monitorBlock {
    display: flex;
    flex-direction: column;
    border-radius: 14px;
    box-sizing: border-box;
    min-height: 0;
}

