/* 키보드 장식 */
.keyboard {
    width: 400px;
    margin: 0 auto;
    border: 2px solid #364e66;
    border-radius: 10px;
    padding: 10px;
    background: #f8fbff;
}

.kbTop {
    display: flex;
    justify-content: center;
    gap: 8px;
    margin-bottom: 8px;
}

.kbGrid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 8px;
    margin-bottom: 8px;
}

.kbBottom {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    gap: 8px;
}

.key {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    background: #f9c300;
    color: #152235;
    border: 2px solid #364e66;
    font-weight: 700;
}

.key.md.empty {
    height: 15px;
    width: 30px;
}



.key.sm {
    height: 18px;
    font-size: 13px;
    text-align: center;
    white-space: nowrap;
}

.key.sm:hover {
    background-color: #364e66;
    color: #eef2ff;
}

.key.md {
    padding: 6px 8px;
    font-size: 12px;
    color: #364e66;
    background-color: #ffffff;
}

.key.md:hover {
    background-color: #364e66;
    color: #eef2ff;
}

.key.lg {
    padding: 8px 10px;
    font-size: 12px;
}

.key.xl {
    padding: 10px 0;
    font-size: 13px;
}

.key.ghost {
    background: #f9c300;
}

/* 오른쪽 정보 */
.projInfo {
    height: 585px;
    background: #fff;
    border-radius: 14px;
    padding: 18px;
    box-sizing: border-box;
    overflow: hidden;
    font-weight: 300;
    font-family: "Sweet", "GyeonggiMillenniumTitle", "맑은 고딕", "Segoe UI", Tahoma, Geneva, sans-serif !important;
    /* 텍스트 길어도 부모 높이 유지 */
}

.projSub {
    margin: 15px 0;
    font-size: 25px;
    color: #364e66;
    font-weight: 800;
}

.proinfo01 p {
    font-size: 16px;
    line-height: 1.5;
    height: 160px;
    /* ✅ 원하는 높이 지정 */
    overflow-y: auto;
    /* ✅ 세로 스크롤 생성 */
    padding-right: 8px;
    /* ✅ 스크롤바와 글 간격 확보 */
}

.proinfo01 p strong {
    color: #c99f0a;
    font-weight: 800;
}

/* 스크롤바 디자인(optional) */
.proinfo01 p::-webkit-scrollbar {
    width: 6px;
}

.proinfo01 p::-webkit-scrollbar-thumb {
    background-color: #f9c300;
    border-radius: 4px;
}

.proinfo01 p::-webkit-scrollbar-track {
    background-color: #364e66;
}

.projSub span {
    padding: 5px 20px;
    border-radius: 20px;
    background-color: #f9c300;
    margin-left: 20px;
}

.projMeta {
    margin: 15px 0;
}


.projMeta p {
    color: #364e66;
}

.projMeta p:first-child {
    margin: 15px 0;
    font-size: 25px;
    font-weight: 800;
}

.projMeta p:nth-child(2) {
    line-height: 1.5;
    font-size: 16px;
}

.projFeat {
    margin: 15px 0;
}

.projFeat h4 {
    margin: 15px 0;
    font-size: 25px;
    color: #22324a;
    font-weight: 800;
}

.projFeat ul {
    font-size: 16px;
}

.projFeat ul li {
    line-height: 1.5;

}

/* 하단 칩 */
.projChips {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0 0;
}

.chip {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
    height: 50px;
    font-size: 30px;
    border-radius: 100px;
    font-weight: 800;
    letter-spacing: -0.5px;
    text-align: center;
    background: #f9c300;
    color: #364e66;
    cursor: pointer;
}

/* 모니터 프레임 */
.monitorFrame {
    width: var(--mf-w, 592px);
    height: var(--mf-h, 419px);
    margin: 0 auto;
    overflow: hidden;
}

.monitorFrame img {
    width: 100%;
    height: 100%;
    object-fit: var(--mf-fit, cover);
}

/* 키보드: 프레임 폭에 어느 정도 맞춰 줄이기 (선택) */
.keyboard {
    width: min(400px, calc(var(--mf-w, 592px) * 0.9));
    /* 프레임 폭의 90% 또는 400 중 더 작은 값 */
    margin: 10px auto 0;
    border: 2px solid #364e66;
    border-radius: 10px;
    padding: 10px;
    background: #f8fbff;
}

.key.sm {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 25px;
    border-radius: 10px;
    background: #ffc61a;
    color: #0e3152;
    font-weight: 500;
    text-decoration: none;
    font-size: 14px;
}

/* 모니터 전체(프레임+받침대) */
.monitorShell {
    position: relative;
    width: 592px;
    /* 모니터 전체 폭 */
    height: 419px;
    /* 모니터 전체 높이 */
    margin: 0 auto;
}

/* 프레임 PNG */
.monitorBody {
    width: 100%;
    height: 100%;
    display: block;
}

/* 모니터 화면(빨간 박스 영역). 위치/크기는 PNG에 맞게 조정 */
.monitorScreen {
    position: absolute;
    top: 7%;
    left: 4%;
    width: 92%;
    height: 72%;
    overflow: hidden;
    border-radius: 8px;
    display: block;
    cursor: pointer;
}

/* 스크린샷: 위에서부터 보이게 (가로맞춤/세로자동) */
.scrollableImage {
    width: 100%;
    height: auto;
    /* 핵심: 세로 자동 */
    display: block;
    object-fit: contain;
    object-position: top center;
    /* 위에서 시작 */
    transform: translateY(0);
}

@keyframes caret {
    50% {
        border-color: transparent;
    }
}

.monitorShell {
    position: relative;
}

.monitorBody {
    position: absolute;
    inset: 0;
    z-index: 2;
    pointer-events: none;
}

.monitorScreen {
    position: absolute;
    z-index: 1;
    overflow: hidden;
}

/* ① 여러 디바이스 가로 정렬 — 4번에 frames가 있을 때만 렌더됨 */
.deviceGrid {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    gap: 32px;
    flex-wrap: wrap;
}

/* ② 프레임 컨테이너: 크기는 JSX(inline)에서 들어옴 */
.monitorShell {
    position: relative;
    margin: 0 auto;
}

/* ③ 프레임 PNG는 위로, 화면은 아래로 겹치기 */
.monitorBody {
    position: absolute;
    inset: 0;
    z-index: 2;
    pointer-events: none;
    width: 100%;
    height: 100%;
    object-fit: contain;
    display: block;
}

.monitorScreen {
    position: absolute;
    z-index: 1;
    overflow: hidden;
    border-radius: 8px;
}

/* ④ 스크롤될 이미지 */
.scrollableImage {
    width: 100%;
    height: auto;
    object-fit: contain;
    object-position: top center;
    display: block;
    will-change: transform;
}

