@charset "UTF-8";

@import url('https://cdn.jsdelivr.net/gh/wanteddev/wanted-sans@v1.0.1/packages/wanted-sans/fonts/webfonts/variable/split/WantedSansVariable.min.css');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-family: "GyeonggiMillenniumTitle", "Sweet", "맑은 고딕", "Segoe UI", Tahoma, Geneva, sans-serif;
}

html {
    scroll-behavior: smooth;
}

body {
    background-color: #ffffff;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
}

li {
    list-style: none;
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
}

a {
    text-decoration: none;
}