.projCard {
  animation: slideIn .35s ease;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(40px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* 선택: 활성 칩 스타일 */
.chip.active {
  background-color: #364e66;
  color: #f9c300;
}

.chip:disabled {
  opacity: .45;
  cursor: not-allowed;
}