:root {
    --header-h: 80px;
    /* 헤더 실제 높이 */
}

* {
    font-family: "GyeonggiMillenniumTitle", "맑은 고딕", "Segoe UI", Tahoma, Geneva, sans-serif !important;
}


#contact {
    width: 100%;
    height: calc(100vh - var(--header-h));
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contactContainer {
    height: 850px;
    margin: 0 auto;
}

.contactMessage {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    color: #364e66;
    font-size: 40px;
    font-weight: 600;
}

.contact-inner {
    width: 1440px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.eggContainer {
    width: 720px;
}

.friedEgg {
    position: relative;
}

.eggYellow {
    position: absolute;
    width: 500px;
    height: 500px;
    border-radius: 50%;
    background-color: #ffcc00;
    top: 70px;
    left: 100px;
}

.text {
    width: 500px;
    height: 450px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    gap: 20px;
}

.qrText {
    font-size: 25px;
    font-weight: 600;
    color: #364e66;
}

.qr-img {
    width: 250px;
    height: 250px;
}

.qrCaption {
    font-size: 18px;
    font-weight: 400;
    color: #364e66;

}

.form-side {
    width: 500px;
    height: 700px;
    display: flex;
    flex-direction: column;
    gap: 40px;
    /* background-color: #ffcc00; */
    align-items: center;
    justify-content: center;
}

.form-side p {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.label {
    font-size: 25px;
    color: #364e66;
}

.input {
    width: 500px;
    height: 50px;
    border: 2px solid #364e66;
    border-radius: 5px;
    font-size: 18px;
    padding: 10px 20px;
}

.form-side #message {
    height: 200px;
}

.submit {
    font-size: 25px;
    width: 100%;
    height: 50px;
    color: #ffffff;
    background-color: #ffcc00;
    cursor: pointer;
}


.submit:hover {
    color: #ffcc00;
    background-color: #364e66;
}

.hoverYellow {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #364e66;
    gap: 70px;
    margin-top: 100px;
}

.hoverPhone {
    display: flex;
    align-items: center;
    justify-content: center;
}

.hoverPhoneN,
.hoverEmailN {
    font-size: 20px;
    font-weight: 500;

}

.hoverEmail {
    display: flex;
    align-items: center;
    justify-content: center;
}

.hoverResponse {
    font-size: 25px;
    font-weight: 500;
}