/* ===== Splash Overlay ===== */
#splash {
    position: fixed;
    /* 화면 전체 덮기 */
    inset: 0;
    z-index: 9999;
    width: 100%;
    height: 100vh;
    background: #fff;
    box-sizing: border-box;
    opacity: 1;
    transform: translateY(0);
    transition: opacity .75s ease, transform .75s ease;
    padding-top: 80px;
}

* {
    font-family: "GyeonggiMillenniumTitle", "맑은 고딕", "Segoe UI", Tahoma, Geneva, sans-serif !important;
}

/* 사라질 때 (JS에서 #splash에 closing 클래스 붙이기) */
#splash.closing {
    opacity: 0;
    transform: translateY(-3%);
    pointer-events: none;
}

#splash .splash-inner {
    width: 100%;
    max-width: 1440px;
    height: 100vh;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    background: #fff;
}

/* ===== Title ===== */
.spTitle {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #ffcc00;
    font-size: 100px;
    line-height: 1.5;
}

.spTitle span {
    text-shadow: 3px 5px 10px rgba(0, 0, 0, .25);
    animation: spotlight 2.5s ease-in-out infinite;
}

@keyframes spotlight {

    0%,
    100% {
        opacity: .8;
        filter: brightness(1);
    }

    50% {
        opacity: .9;
        filter: brightness(1.3);
    }
}

/* ===== Egg ===== */
.egg-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 556px;
}

.hint {
    font-size: 50px;
    color: #364e66;
    font-weight: 600;
    margin-bottom: 8px;
}

.egg-wrap .egg {
    position: relative;
    width: 556px;
    height: 700px;
    border-radius: 50%;
    cursor: pointer;
    background: radial-gradient(circle at 70% 40%, #ffffff 0%, #ffffff 60%, #cfcfcf 100%);
    box-shadow:
        inset -25px -35px 60px rgba(0, 0, 0, .25),
        inset 12px 12px 30px rgba(255, 255, 255, .6),
        0 8px 20px rgba(0, 0, 0, .15);
    transition: box-shadow .5s ease;
}

/* 클릭 시 밝아지는 플래시 */
.egg.lit {
    animation: shadow-flash .9s ease-out;
}

@keyframes shadow-flash {
    0% {
        box-shadow:
            inset -25px -35px 60px rgba(0, 0, 0, .25),
            inset 12px 12px 30px rgba(255, 255, 255, .6),
            0 8px 20px rgba(0, 0, 0, .15);
        filter: brightness(1);
    }

    30% {
        box-shadow:
            inset -10px -20px 70px rgba(255, 240, 180, .7),
            inset 20px 20px 50px rgba(255, 255, 255, .9),
            0 0 60px rgba(255, 240, 150, .6);
        filter: brightness(1.3);
    }

    100% {
        box-shadow:
            inset -25px -35px 60px rgba(0, 0, 0, .25),
            inset 12px 12px 30px rgba(255, 255, 255, .6),
            0 8px 20px rgba(0, 0, 0, .15);
        filter: brightness(1);
    }
}

/* ===== Crack (hover 시 표시) ===== */
.egg-wrap .egg::after {
    content: "";
    position: absolute;
    inset: 0;
    pointer-events: none;
    opacity: 0;
    transition: opacity .25s ease;

    background-repeat: no-repeat;
    /* 위치(X% Y%)과 크기(가로 x 세로)는 필요에 맞게 조절 */
    background-position: 58% 6%;
    background-size: 170px 420px;

    background-image: url("data:image/svg+xml;utf8,\
  <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 200 500'>\
    <path d='M200 20 L80 260 L150 300 L80 460' \
      stroke='%23000' stroke-width='6' fill='none' \
      stroke-linecap='round' stroke-linejoin='round'/>\
  </svg>");
}

.egg-wrap .egg:hover::after {
    opacity: 1;
}

/* ===== Responsive (optional) ===== */
@media (max-width: 980px) {
    .spTitle {
        font-size: 72px;
    }

    .egg-wrap {
        width: 420px;
    }

    .egg-wrap .egg {
        width: 420px;
        height: 530px;
    }

    .hint {
        font-size: 36px;
    }
}