/* header.css */
* {
  font-family: "GyeonggiMillenniumTitle", "맑은 고딕", "Segoe UI", Tahoma, Geneva, sans-serif !important;
}

.menuBar {
  position: sticky;
  top: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100px;
  margin: 0 auto;
  background: #ffffff;
}

.menuBar img {
  width: 80px;
  height: 80px;
  margin-left: 20px;
  /* ✅ 추가 */
  flex: 0 0 auto;
  /* ✅ 안전하게 고정 */
}


/* 컨테이너 UL 초기화 + 가로 정렬 */
.menuBar .moveBar {
  width: min(1440px, 100%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  /* 기본 UL 패딩 제거 */
  margin: 0 auto;
  /* 기본 UL 마진 제거 */
  list-style: none;
  /* 불릿 제거 */
}

/* LI 초기화(선택) */
.menuBar .moveBar li {
  list-style: none;
}

/* 버튼 스타일 (a 대신 button) */
.menuBar .moveBar li button {
  border: none;
  background: transparent;
  cursor: pointer;
  font-weight: bold;
  color: #364e66;
  font-size: 35px;
  line-height: 1;
  padding: 4px 8px;
  /* 클릭 영역 확보 */
  transition: color 0.3s;
}


/* hover/active 색상 */
.menuBar .moveBar li button:hover,
.menuBar .moveBar li button.active {
  color: #f9c300;
}

/* 섹션 오프셋(헤더 높이만큼) */
#main,
#about,
#project,
#contact {
  scroll-margin-top: 100px;
  /* 헤더 높이에 맞춤 */
}

/* 선택: CSS로 전체 부드러운 스크롤 */
html {
  scroll-behavior: smooth;
}

/* 선택: 반응형 폰트 축소 */
@media (max-width: 1024px) {
  .menuBar .moveBar li button {
    font-size: 28px;
  }
}

@media (max-width: 768px) {
  .menuBar {
    height: 72px;
  }

  .menuBar .moveBar li button {
    font-size: 22px;
  }

  #main,
  #about,
  #project,
  #contact {
    scroll-margin-top: 72px;
  }
}