:root {
  --header-h: 80px;
  /* 헤더 실제 높이 */
}

* {
  font-family: "GyeonggiMillenniumTitle", "맑은 고딕", "Segoe UI", Tahoma, Geneva, sans-serif !important;
}


#main {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: calc(100vh - var(--header-h));
  box-sizing: border-box;
  /* scroll-margin-top: 80px; */

}

#main .intro {
  display: flex;
  align-items: center;
  justify-content: space-between;
  scroll-margin-top: 80px;
  margin: 0 auto;
  width: 100%;
  max-width: 1440px;
  height: 850px;
  background-color: #ffffff;

  box-sizing: border-box;
}

.introText {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: flex-start;
  justify-content: center;
  gap: 40px;
}

.introText h1 {
  font-size: 100px;
  color: #ffcc00;
  line-height: 1.3;
}

.introText h1 span {
  text-shadow:
    1px 0 #8d8d8f,
    -1px 0 #8d8d8f,
    0 1px #8d8d8f,
    0 -1px #8d8d8f;
  font-weight: 600;
}

.introText h1 span strong {
  font-size: 100px;
  color: #ffb100;
  font-weight: 600;
}

.introText h1 span:nth-of-type(2) strong,
.introText h1 span:nth-of-type(3) strong {
  font-size: 88px;
}

.introText p {
  font-size: 35px;
  color: #364e66;
  font-weight: 400;
  line-height: 1.8;
  text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
  margin-left: 2px;
}

.introImage {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 556px;
  height: 700px;
  border-radius: 50%;
  background-color: #ececec;
  overflow: hidden;
  /* 원형 바깥 이미지 잘림 방지 */
}

.introImage img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  /* 이미지가 원형 안에 꽉 차도록 */
}