/* 좌측 프로필 카드(이미지 + 오버레이 + 다운로드 링크) */
.aboutLeftImage {
    position: relative;
    display: block;
    /* 링크가 이미지 크기를 그대로 가짐 */
    width: 364px;
    height: 525px;
    margin-bottom: 10px;
    border-radius: 12px;
    overflow: hidden;
    /* 확대 시 밖으로 안 튀게 */
    text-decoration: none;
    /* a 태그일 때 밑줄 제거 */
    color: inherit;
}

/* 이미지: 부모 박스 크기에 맞춰 담기 (왜곡 방지) */
.aboutLeftImage img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: inherit;
    /* 모서리 라운드 정확히 일치 */
    vertical-align: top;
    /* 혹시 모를 인라인 정렬 여백 제거 */
    /* ← fill 대신 cover */
    transform: translateZ(0) scale(1);
    transition: transform .28s ease;
    line-height: 0;
    /* 베이스라인 간격 제거 */
    background: #fff;
    /* 배경색 지정(선이 보일 여지 제거) */
    backface-visibility: hidden;
    will-change: transform;
}

/* 만약 아직 0.5px 틈이 보이면 마지막으로 이것도 추가 */
@supports (height: calc(100% + 1px)) {
    .aboutLeftImage img {
        height: calc(100% + 0.5px);
    }
}

/* hover 시 '이미지'만 살짝 확대 (부모는 그대로라 아래 글자 안 가려짐) */
.aboutLeftImage:hover img {
    transform: scale(1.04);
}

/* 오버레이: 항상 이미지와 정확히 겹침 */
.aboutLeftImage .overlay {
    position: absolute;
    inset: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, .45);
    color: #fff;
    font-weight: 700;
    letter-spacing: .02em;
    opacity: 0;
    transition: opacity .2s ease;
    pointer-events: none;
    /* 클릭은 부모(a)가 받음 → 다운로드 정확히 동작 */
}

/* hover 시 오버레이만 나타남 */
.aboutLeftImage:hover .overlay {
    opacity: 1;
}